import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { withStyles } from '@material-ui/core/styles';
import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import HomeIcon from '@material-ui/icons/Home';
import PeopleIcon from '@material-ui/icons/People';
import IconButton from '@material-ui/core/IconButton';
import DnsRoundedIcon from '@material-ui/icons/DnsRounded';
import PermMediaOutlinedIcon from '@material-ui/icons/PhotoSizeSelectActual';
import PublicIcon from '@material-ui/icons/Public';
import SettingsEthernetIcon from '@material-ui/icons/SettingsEthernet';
import SettingsInputComponentIcon from '@material-ui/icons/SettingsInputComponent';
import TimerIcon from '@material-ui/icons/Timer';
import SettingsIcon from '@material-ui/icons/Settings';
import PhonelinkSetupIcon from '@material-ui/icons/PhonelinkSetup';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import {connect} from 'react-redux';
import LoyaltyIcon from '@material-ui/icons/Loyalty';
import {Link,withRouter} from 'react-router-dom';
import {toggleDrawer} from './actions/index';
import { Typography } from '@material-ui/core';
import Collapse from '@material-ui/core/Collapse';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore'
import AssignmentIcon from '@material-ui/icons/Assignment';
const drawerWidth = 220;
const categories = [
  {
    id: 'Develop',
    children: [
      { id: 'Authentication', icon: <PeopleIcon />, active: true },
      { id: 'Database', icon: <DnsRoundedIcon /> },
      { id: 'Storage', icon: <PermMediaOutlinedIcon /> },
      { id: 'Hosting', icon: <PublicIcon /> },
      { id: 'Functions', icon: <SettingsEthernetIcon /> },
      { id: 'ML Kit', icon: <SettingsInputComponentIcon /> },
    ],
  },
  {
    id: 'Quality',
    children: [
      { id: 'Analytics', icon: <SettingsIcon /> },
      { id: 'Performance', icon: <TimerIcon /> },
      { id: 'Test Lab', icon: <PhonelinkSetupIcon /> },
    ],
  },
];

const styles = theme => ({
  categoryHeader: {
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  categoryHeaderPrimary: {
    color: theme.palette.common.white,
  },
  item: {
    paddingTop: 1,
    paddingBottom: 1,
    color: 'rgba(255, 255, 255, 0.7)',
    '&:hover,&:focus': {
      backgroundColor: 'rgba(255, 255, 255, 0.08)',
    },
  },
  itemCategory: {
    backgroundColor: '#232f3e',
    boxShadow: '0 -1px 0 #404854 inset',
    paddingTop: theme.spacing(2),
    paddingBottom: theme.spacing(2),
  },
  firebase: {
    fontSize: 24,
    color: theme.palette.common.white,
  },
  itemActiveItem: {
    color: '#4fc3f7',
  },
  itemPrimary: {
    fontSize: 'inherit',
  },
  itemIcon: {
    minWidth: 'auto',
    marginRight: theme.spacing(2),
  },
  divider: {
    marginTop: theme.spacing(2),
  },
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
   
    display: 'flex',
    alignItems: 'center',
    
    padding: '0 8px',
    color: 'rgba(255, 255, 255, 0.7)',
    '&:hover,&:focus': {
      backgroundColor: 'rgba(255, 255, 255, 0.08)',
    },
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    
  
    backgroundColor:'rgba(0, 0, 0, 0.87)',
    
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
       
    width: drawerWidth,
   
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '100vh',
    backgroundColor:'#fff',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
    backgroundColor:'#fff'
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
  
});

function Navigator(props) {
  console.log(props)
  const { classes, ...other } = props;
  const [open, setOpen] = React.useState({
    Users:false,
    Orders:false,
    Config:false,
    Products:false

  });
  const handleOpen = (open2) => {
    setOpen({...open,...open2});
  };

  console.log(props.drawer)
  return (
    <Drawer variant={props.variant} open={props.drawer}
    classes={{
      paper: clsx(props.drawer&&classes.drawerPaper, !props.drawer && classes.drawerPaperClose),
    }}
    onClose={props.toggleDrawer}
    >
    
    <div style={{position:'fixed',backgroundColor:'black',zIndex:1000,width:drawerWidth}} className={classes.toolbarIcon}>
          <Typography>VinderComm</Typography>
          <IconButton style={{justifyContent: 'flex-end',float:'right'}} onClick={props.toggleDrawer}>
            <ChevronLeftIcon style={{color:'white'}} />
          </IconButton>
        </div>
        <div className={classes.toolbarIcon}>
       
        </div>
      <List disablePadding>
     
        <ListItem className={clsx(classes.item, classes.itemCategory)}>
          <ListItemIcon className={classes.itemIcon}>
            <HomeIcon />
          </ListItemIcon>
          <ListItemText
            classes={{
              primary: classes.itemPrimary,
            }}
          >
            Dashboard
          </ListItemText>
        </ListItem>
       
          <React.Fragment key={"Users"}>
            <ListItem className={clsx(classes.item, classes.itemCategory)}
            button onClick={()=>handleOpen({"Users":!open['Users']})}>
              <ListItemIcon className={classes.itemIcon}> <PeopleIcon style={{color:'white'}} /></ListItemIcon>
              <ListItemText
                classes={{
                  primary: classes.categoryHeaderPrimary,
                }}
              >
                {'Users'}
              </ListItemText>
              {open['Users'] ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={open['Users']} timeout="auto" unmountOnExit>
           <ListItem
                key={"Customers"}
                button
             onClick={()=>props.history.push("/user/customers")}
                className={clsx(classes.item)}
              >
              
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Customers'}
                </ListItemText>
                
              </ListItem>
              <Divider  style={{color:'white',width:'100%'}}/>

              <ListItem
                key={"VLEs"}
                button
                onClick={()=>props.history.push("/user/vle")}
                className={clsx(classes.item)}
              >
                
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'VLEs'}
                </ListItemText>
               
              </ListItem>
              <Divider style={{color:'white',width:'100%'}}/>
              <ListItem
                key={"Admins"}
                button
                onClick={()=>props.history.push("/user/admins")}
                className={clsx(classes.item)}
              >
                
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Admins'}
                </ListItemText>
               
              </ListItem>
              <Divider  style={{color:'white',width:'100%'}}/>
              <ListItem
                key={"Sellers"}
                button
                onClick={()=>props.history.push("/user/seller")}
                className={clsx(classes.item)}
              >
          
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Sellers'}
                </ListItemText>
              
              </ListItem>
              </Collapse>
              <Divider  style={{color:'white',width:'100%'}}/>

          </React.Fragment>


          <React.Fragment key={"Orders"} >
            <ListItem 
             button onClick={()=>handleOpen({"Orders":!open['Orders']})}
            className={clsx(classes.item, classes.itemCategory)}>
            <ListItemIcon className={classes.itemIcon}> <ShoppingCartIcon style={{color:'white'}} /></ListItemIcon>
              <ListItemText
                classes={{
                  primary: classes.categoryHeaderPrimary,
                }}
              >
                {'Orders'}
              </ListItemText>
              {open['Orders'] ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={open['Orders']} timeout="auto" unmountOnExit>
              <ListItem
                key={"All orders"}
                button
                onClick={()=>props.history.push("/orders/all")}
                className={clsx(classes.item)}
              >
               
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'All orders'}
                </ListItemText>
              </ListItem>
              <Divider  style={{color:'white',width:'100%'}}/>
              <ListItem
                key={"Unprocessed orders"}
                onClick={()=>props.history.push("/orders/unprocessed")}
                button
                className={clsx(classes.item)}
              >
                
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {"Unprocessed orders"}
                </ListItemText>
              </ListItem>
             </Collapse>
          

          
          <Divider  style={{color:'white',width:'100%'}}/>
          </React.Fragment>
          
          <React.Fragment key={"Products"}>
            <ListItem 
            onClick={()=>handleOpen({"Products":!open['Products']})}
            className={clsx(classes.item, classes.itemCategory)}>
            <ListItemIcon className={classes.itemIcon}> <LoyaltyIcon style={{color:'white'}} /></ListItemIcon>
              <ListItemText
                classes={{
                  primary: classes.categoryHeaderPrimary,
                }}
              >
                {'Products'}
              </ListItemText>
              {open['Products'] ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
           
            <Collapse in={open['Products']} timeout="auto" unmountOnExit>
              <ListItem
                key={"All Products"}
                button
                onClick={()=>props.history.push("/products/all")}
                className={clsx(classes.item)}
              >
               
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'All Products'}
                </ListItemText>
              </ListItem>
              <Divider  style={{color:'white',width:'100%'}}/>
              <ListItem
                key={"Add Product"}
                button
                onClick={()=>props.history.push("/products/add")}
                className={clsx(classes.item)}
              >
               
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Add Product'}
                </ListItemText>
              </ListItem>
              <Divider  style={{color:'white',width:'100%'}}/>
              <ListItem
                key={"Manage Category"}
                button
                onClick={()=>props.history.push("/products/category")}
                className={clsx(classes.item)}
              >
                
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Manage Category'}
                </ListItemText>
              </ListItem>
             
</Collapse>
          

            <Divider  style={{color:'white',width:'100%'}}/>
          </React.Fragment>
          <React.Fragment key={"Configuration"}>
            <ListItem
            onClick={()=>handleOpen({"Config":!open['Config']})}
            className={clsx(classes.item, classes.itemCategory)}>
            <ListItemIcon className={classes.itemIcon}> <SettingsIcon style={{color:'white'}} /></ListItemIcon>
              <ListItemText
                classes={{
                  primary: classes.categoryHeaderPrimary,
                }}
              >
                {'Configuration'}
              </ListItemText>
              {open['Config'] ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={open['Config']} timeout="auto" unmountOnExit>
              <ListItem
                key={"General"}
                button
                onClick={()=>props.history.push("/config/general")}
                className={clsx(classes.item)}
              >
             
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'General'}
                </ListItemText>
              </ListItem>
              <Divider  style={{color:'white',width:'100%'}}/>
              <ListItem
                key={"Tax rules"}
                button
                onClick={()=>props.history.push("/config/tax")}
                className={clsx(classes.item)}
              >
              
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Tax rules'}
                </ListItemText>
              </ListItem>
              <Divider  style={{color:'white',width:'100%'}}/>
              <ListItem
                key={"Servicable States"}
                button
                onClick={()=>props.history.push("/config/states")}
                className={clsx(classes.item)}
              >
   
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Servicable State'}
                </ListItemText>
              </ListItem>
       </Collapse>
          
              <Divider  style={{color:'white',width:'100%'}}/>
          </React.Fragment>

            <React.Fragment key={"Reports"}>
            <ListItem
            onClick={()=>handleOpen({"Reports":!open['Reports']})}
            className={clsx(classes.item, classes.itemCategory)}>
            <ListItemIcon className={classes.itemIcon}> <AssignmentIcon style={{color:'white'}} /></ListItemIcon>
              <ListItemText
                classes={{
                  primary: classes.categoryHeaderPrimary,
                }}
              >
                {'Reports'}
              </ListItemText>
              {open['Reports'] ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={open['Reports']} timeout="auto" unmountOnExit>
              <ListItem
                key={"Sales"}
                button
                onClick={()=>props.history.push("/report/daily")}
                className={clsx(classes.item)}
              >
             
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Sales Report'}
                </ListItemText>
              </ListItem>
              <Divider  style={{color:'white',width:'100%'}}/>
              <ListItem
                key={"Products Report"}
                button
                onClick={()=>props.history.push("/report/product")}
                className={clsx(classes.item)}
              >
              
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Products Report'}
                </ListItemText>
              </ListItem>
              <Divider  style={{color:'white',width:'100%'}}/>
              <ListItem
                key={"Customers report"}
                button
                onClick={()=>props.history.push("/report/customer")}
                className={clsx(classes.item)}
              >
   
                <ListItemText
                  classes={{
                    primary: classes.itemPrimary,
                  }}
                >
                  {'Customers report'}
                </ListItemText>
              </ListItem>
       </Collapse>
          
              <Divider  style={{color:'white',width:'100%'}}/>
          </React.Fragment>
      </List>
    </Drawer>
  );
}

Navigator.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = state => {
  console.log(state.product.attachment)
      return {
        attachment:state.product.attachment,
        drawer:state.common.drawer
       }
  };
  

export default withRouter(withStyles(styles)(connect(mapStateToProps,{toggleDrawer}) (Navigator)));