import React,{Component} from 'react';

import Title from '../Title';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import MenuItem from '@material-ui/core/MenuItem';
import {UploadEditorImg} from '../../utilities/utilFun'
import {updateAttachment,updateProduct,snackbar,setConfig} from '../../actions'
import {connect} from 'react-redux';
import {store} from '../../App.js'
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab';
import axios from 'axios'
import  Hidden from '@material-ui/core/Hidden'
import Skeleton from '@material-ui/lab/Skeleton';
import ChipInput from 'material-ui-chip-input'
import ConfigState from './configState'
import ConfigTax from './configTaxes'
import {

    withRouter
  } from "react-router-dom";
class Config extends Component {
  constructor(props) {
    super(props);
    this.state={
      email:'',
      contact:'',
      msgAuth:'',
      address:'',
      salt:'',
      GST:'',
      tax:[],
      transactionCharge:0,

      state:[],
      _id:0
    }
    this.handleAddCategory=this.handleAddCategory.bind(this)
    this.handleDeleteCategory=this.handleDeleteCategory.bind(this)
this.handleField=this.handleField.bind(this)
this.handleUpdate=this.handleUpdate.bind(this)
  }

  componentDidMount=()=>{
  axios.post('https://green-planet.herokuapp.com/public/config/get',{}).then(res=>{
    if(res.data){
      console.log(res.data[0])
      this.setState({...res.data[0]})
    }
  })
  }

  handleUpdate=()=>{
  var  data={...this.state}

    var stateData=[]
    var tax=[]

    data.state.map(val=>{
      stateData.push({stateCode:val.stateCode,name:val.name,service:val.service})
    })
    data.tax.map(val=>{
      tax.push({HSN:val.HSN,CGST:val.CGST,SGST:val.SGST,IGST:val.IGST})
    })

    data.tax=tax

    axios.post('https://green-planet.herokuapp.com/public/config/update',{...data}).then(res=>{
      console.log(res.data)
      if (res.data.nModified>-1){
        this.props.snackbar({sType:'success',open:true,message:'Successfuly updated configuration'})
       this.props.setConfig({...data})
      }
      else {
          this.props.snackbar({sType:'error',open:true,message:'Opps something went wrong!'})
      }
    })

  }

  handleField=(e)=>{
    this.setState({[e.target.id]:e.target.value})
  }

  handleAddCategory=(chip)=>{
      var data=this.state.category
      console.log(chip)
if(data.indexOf(chip.toLowerCase())<0){
  data.push(chip)
  this.setState({category:data})
}
}

  handleDeleteCategory=(chip, index)=>{
    var data=this.state.category
    data.splice(index,1)
    this.setState({category:data})
  }

render(){


  return (
    <React.Fragment>


      <form style={{width:'100%'}} >
      <Grid container spacing={3}  justify={'center'} >
  

    


<Grid item xs={12}>
<ConfigState setState={(val)=>this.setState(val)} data={this.state.state}/>

</Grid>
<Grid item xs={12}>
<Button variant='contained' fullWidth color='primary' onClick={this.handleUpdate} style={{padding:"15px"}}><AddIcon style={{marginRight:'15px'}}/>{'Save Configuration'}</Button>
</Grid>
      </Grid>
      </form>


    </React.Fragment>
  );
}

}


const mapStateToProps = state => {

    return {
      attachment:state.product.attachment,
      products:state.product.products
     }
};

export default withRouter(connect(mapStateToProps,{updateAttachment,updateProduct,snackbar,setConfig})(Config));
