import React from 'react';
import MaterialTable from 'material-table';
import { forwardRef } from 'react';
import CreateProduct from './createProduct'
import AddBox from '@material-ui/icons/AddBox';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import HeaderSecondary from '../secondaryAppbar'
import Divider from '@material-ui/core/Divider'
import Tabs from '../headerTabs';
import axios from 'axios';
import Shipping from '../components/shipping/shippngTab'
const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
    DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => <ArrowUpward {...props} ref={ref} />),
    ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
  };

class MaterialTableDemo extends React.Component {
  constructor(props) {
    super(props);
this.state={
  add:false,
  loading:false,
  filter:'All',
  columns: [
    { title: 'Product Image', field: 'image',editable: 'never' ,cellStyle: {
      fontSize:'12px'
    },

    render: rowData => <div style={{display:'flex'}}>
    <img src={rowData.image} style={{width:'80px',height:'auto'}}/>

        </div>



},
    
    { title: 'Title', field: 'title' },
    { title: 'Quantity available', field: 'quantity', type: 'numeric' },
    { title: 'Price', field: 'price', type: 'numeric' },
    { title: 'Price-VLE', field: 'priceVLE', type: 'numeric' },
  ],
  data: [
    { _id: 'skdkj', title: 'Baran', quantity: 1987, price: 63,priceVLE:45 },

  ],

}
this.setFilter=this.setFilter.bind(this)

  }

  setFilter(newValue){
    console.log(newValue)
this.setState({filter:newValue},()=>{
  if(newValue=='All'){
    this.componentDidMount({})
  }
  if(newValue=='Published'){
    this.componentDidMount({  isPublished:true  })
  }
  if(newValue=='Unpublished'){
    this.componentDidMount({  isPublished:false  })
  }
  if(newValue=='Out of stock'){
    this.componentDidMount({  quantity:0  })
  }
})

  }

  componentDidMount(params={}){
    this.setState({loading:true})

    console.log(params)
    axios.post('https://green-planet.herokuapp.com/public/product/list',{...params}).then(res=>{
   if(res.data){
     var pdata=[]
     res.data.map(data=>{
       pdata.push({_id:data._id,image:data.images[0],title:data.title,quantity:data.quantity,...data.pricing})
     })
     console.log(pdata)
     this.setState({data:pdata,loading:false})
   }

    })
  }


render() {


  return (
    <React.Fragment>
      <HeaderSecondary title="Products"/>
      <Tabs filter={this.state.filter} onChange={this.setFilter}/>
    {this.state.add&&<CreateProduct handleAdd={()=>this.setState({add:false})}/>}
  { !this.state.add&& <MaterialTable
  style={{width:'100%',boxShadow:'none'}}
    icons={tableIcons}
    options={{
     exportButton: true
   }}
    size='large'
      title={this.state.filter+" Products"}
      isLoading={this.state.loading}
      columns={this.state.columns}
      data={this.state.data}
      actions={[
             {
               icon: AddBox,
               tooltip: 'All',
               isFreeAction: true,
               onClick: (event) => this.props.history.push('/products/Add')
             }
           ]}


    onRowClick={(event, rowData, togglePanel) => this.props.history.push('/editproduct/'+rowData._id)}
    />}
    </React.Fragment>
  );
}

}
export default MaterialTableDemo
