import { render } from 'react-dom';
import 'react-sortable-tree/style.css';
import React, { Component } from 'react';
import SortableTree, { addNodeUnderParent, removeNodeAtPath , changeNodeAtPath } from 'react-sortable-tree';
import HTML5Backend from "react-dnd-html5-backend";
import { DndProvider } from "react-dnd";
import Grid from '@material-ui/core/Grid'
import {connect} from 'react-redux';
import {store} from '../../App.js'
import Button from '@material-ui/core/Button'
import {snackbar,setConfig,toggleLoader} from '../../actions'
import {withRouter} from "react-router-dom";
import axios from 'axios'
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import IconButton from '@material-ui/core/IconButton';
import Title from '../Title'

const firstNames = [
  'Abraham',
  'Adam',
  'Agnar',
  'Albert',
  'Albin',
  'Albrecht',
  'Alexander',
  'Alfred',
  'Alvar',
  'Ander',
  'Andrea',
  'Arthur',
  'Axel',
  'Bengt',
  'Bernhard',
  'Carl',
  'Daniel',
  'Einar',
  'Elmer',
  'Eric',
  'Erik',
  'Gerhard',
  'Gunnar',
  'Gustaf',
  'Harald',
  'Herbert',
  'Herman',
  'Johan',
  'John',
  'Karl',
  'Leif',
  'Leonard',
  'Martin',
  'Matt',
  'Mikael',
  'Nikla',
  'Norman',
  'Oliver',
  'Olof',
  'Olvir',
  'Otto',
  'Patrik',
  'Peter',
  'Petter',
  'Robert',
  'Rupert',
  'Sigurd',
  'Simon',
];

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      treeData: [{ title: 'Peter Olofsson' }, { title: 'Karl Johansson' }],
      addAsFirstChild: false,

    };
  }

  componentDidMount=()=>{
    this.props.toggleLoader()
  axios.post('https://green-planet.herokuapp.com/public/config/get',{}).then(res=>{
    if(res.data){
      this.props.toggleLoader()
      console.log(res.data[0])
      this.setState({_id:res.data[0]._id,treeData:res.data[0].category})
    }
  })
  }

  normalize=(tree)=>{
    console.log(tree)
    if(tree.length==0)
    return []
    var treeData=[]
    tree.map(data=>{
      if(data.children)
      treeData.push({value:data.title,key:data.title,children:this.normalize(data.children)})
      else {
          treeData.push({value:data.title,key:data.title})
      }
    })
    console.log(treeData)
    return treeData
  }

  saveConfig=()=>{
    axios.post('https://green-planet.herokuapp.com/public/config/update',{category:this.state.treeData,_id:this.state._id}).then(res=>{
      console.log(res.data)
      if (res.data.nModified>-1){
        this.props.snackbar({sType:'success',open:true,message:'Successfuly updated configuration'})
this.props.setConfig({category:this.state.treeData})
      }
      else {
          this.props.snackbar({sType:'error',open:true,message:'Opps something went wrong!'})
      }
    })
  }

  render() {
    const getNodeKey = ({ treeIndex }) => treeIndex;
    const getRandomName = () =>
      firstNames[Math.floor(Math.random() * firstNames.length)];
    return (

<Grid container>
  <Grid item xs={12}><Title>{'Manage Category'}</Title></Grid>
<Grid item xs={12} style={{height:'75vh'}}>
  <SortableTree

    treeData={this.state.treeData}
    onChange={treeData => this.setState({ treeData })}
    generateNodeProps={({ node, path }) => ({
      title: (
        <input
          style={{ fontSize: '1.1rem' }}
          value={node.title}
          onChange={event => {
            const name = event.target.value;

            this.setState(state => ({
              treeData: changeNodeAtPath({
                treeData: state.treeData,
                path,
                getNodeKey,
                newNode: { ...node, title:name },
              }),
            }));
          }}
        />
      ),
      buttons: [

        <IconButton size='small' edge="end" aria-label="add sub category" onClick={() =>
          this.setState(state => ({
            treeData: addNodeUnderParent({
              treeData: state.treeData,
              parentKey: path[path.length - 1],
              expandParent: true,
              getNodeKey,
              newNode: {
                title: 'new sub category',
              },
              addAsFirstChild: state.addAsFirstChild,
            }).treeData,
          }))
        }>
          <AddIcon/>
        </IconButton>,
        <IconButton size='small' edge="end" aria-label="Delete" onClick={() =>
          this.setState(state => ({
            treeData: removeNodeAtPath({
              treeData: state.treeData,
              path,
              getNodeKey,
            }),
          }))
        }>
        <DeleteIcon/>
        </IconButton>

      ],
    })}
  />
</Grid>
<Grid item xs={12}>
  <Grid container>
<Grid item xs={4}>
  <Button onClick={() =>
  this.setState(state => ({
    treeData: state.treeData.concat({
      title: 'new Category',
    }),
  }))
} variant='contained'>Add new category</Button></Grid>
<Grid item xs={4}><Button variant='contained' onClick={this.saveConfig}>save</Button></Grid>
  </Grid>



</Grid>
</Grid>





    );
  }
}

const mapStateToProps = state => {

    return {
      attachment:state.product.attachment,
      products:state.product.products
     }
};

export default withRouter(connect(mapStateToProps,{snackbar,setConfig,toggleLoader})(App));
