import React, { Component } from 'react';
import {Provider} from 'react-redux';
import {createStore } from 'redux';
import rootReducer from './reducers';
import ResetPass from './components/Auth/forgotpass'
import {BrowserRouter, Switch, Route, Redirect} from 'react-router-dom';
import Login from './components/Auth/login'

import Dashboard from "./adminPages/Dashboard";
import Header from "./components/Header/Header";
import Footer from "./components/Footer/Footer";
import ProductDetail from "./pages/ProductDetail/ProductDetail";
import ShoppingCart from "./pages/ShopingCart/ShoppingCart";
import Product from "./pages/Home/Home"
import Snackbar from "./components/message/snackbar"
import Singin from "./adminPages/signin"
import requireAuth from './utilities/requireAuth'
import axios from 'axios'
import HTML5Backend from "react-dnd-html5-backend";
import { DndProvider } from "react-dnd";
import Paperbase from "./Paperbase";
export const  store = createStore(rootReducer, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__());

class App extends Component {

  componentWillMount=()=>{



if(localStorage.Auth)
  {
    var sess=JSON.parse(localStorage.Auth)
  console.log(sess)
  if(sess!=null)
  store.dispatch({type: 'SET_USER', data:{user: sess.user,token:sess.token}})
}
}
componentDidMount=()=>{
  axios.post('https://green-planet.herokuapp.com/public/config/get',{}).then(res=>{
    if(res.data){
      console.log(res.data[0])
      store.dispatch({type:'SETCONFIG',config:{...res.data[0]}})
      this.setState({...res.data[0]})
    }
  })
}

  render() {
    return (  <DndProvider backend={HTML5Backend}>
     
        <Provider store={store}>
            <BrowserRouter>
            <Switch>
              <Route exact path={'/login'}
                component={Login} />
                <Route exact path={'/resetPassword'}
                  component={ResetPass} />
                <Route path={'/'} component={requireAuth(Paperbase)} />
            </Switch>

          </BrowserRouter>

        <Snackbar/>

        </Provider>
    
      </DndProvider>
    );
  }
}

export default App;
