import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Chip from '@material-ui/core/Chip';
import Paper from '@material-ui/core/Paper';
import TagFacesIcon from '@material-ui/icons/TagFaces';
import Button from '@material-ui/core/Button'
import Dialoge from './Dialoge.js'
const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    backgroundColor:'none',
    flexWrap: 'wrap',
    boxShadow:'none',
    padding: theme.spacing(3),
  },
  chip: {
    margin: theme.spacing(0.5),
  },
}));

export default function ChipsArray(props) {
  const classes = useStyles();
  const [chipData, setChipData] = React.useState([
    { key: 0, label: 'Angular' },
    { key: 1, label: 'jQuery' },
    { key: 2, label: 'Polymer' },
    { key: 3, label: 'React' },
    { key: 4, label: 'Vue.js' },
  ]);
  const [open, setOpen] = React.useState(false);


    const handleClickOpen = () => {
      setOpen(true);
    };
  const handleClose = () => {
    setOpen(false);
  };

  const handleDelete = chipToDelete => () => {
    setChipData(chips => chips.filter(chip => chip.key !== chipToDelete.key));
  };
console.log('props.config')

  return (
    <Paper elevation={0} className={classes.root}>
      {props.checked.map(data => {



        return (
          <Chip
            key={data}

            label={data}
            className={classes.chip}
          />
        );
      })}
      <Button onClick={handleClickOpen} variant='contained' color='primary' style={{paddingLeft:'20px',paddingRight:'20px',marginLeft:'40px'}}>
Edit
      </Button>
      <Dialoge handleClose={handleClose} checked={props.checked} setChecked={props.setChecked} open={open} config={props.config}/>
    </Paper>
  );
}
