import React from 'react';
import CheckboxTree from 'react-checkbox-tree';
import 'react-checkbox-tree/lib/react-checkbox-tree.css';
import axios from 'axios'
const nodes = [{
    value: 'mars',
    label: 'Mars',
    children: [
        { value: 'phobos', label: 'Phobos' ,children:[{value: 'phoboss', label: 'Phoboss'}]},
        { value: 'deimos', label: 'Deimos' },
    ],
}];

class Widget extends React.Component {
    constructor() {
        super();

        this.state = {
            checked: [],
            expanded: [],
            nodes:[],
        };
    }
    componentDidMount=()=>{
        this.setState({nodes:this.normalize(this.props.config)})
        console.log(this.props)
  
    }
    normalize=(tree)=>{
      console.log(tree)
      if(tree.length==0)
      return []
      var treeData=[]
      tree.map(data=>{
        if(data.children)
        treeData.push({value:data.title,label:data.title,children:this.normalize(data.children)})
        else {
            treeData.push({value:data.title,label:data.title})
        }
      })
      console.log(treeData)
      return treeData
    }

    render() {
        return (
            <CheckboxTree
                nodes={this.state.nodes}
                checked={this.props.checked}
                expanded={this.state.expanded}
                onCheck={checked => {
                  this.props.setChecked(checked)
                  this.setState({ checked })}}
                onExpand={expanded => this.setState({ expanded })}
            />
        );
    }
}


export default Widget
