import React from 'react';
import MaterialTable from 'material-table';
import { forwardRef } from 'react';
import CreateProduct from './createProduct'
import AddBox from '@material-ui/icons/AddBox';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import axios from 'axios';
import {connect} from 'react-redux';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import Details from './orderDetails'
import Tabs from '../orderTab'
import HeaderSecondary from '../secondaryAppbar'
import { DateTimePicker,MuiPickersUtilsProvider } from "@material-ui/pickers";
import Button from '@material-ui/core/Button';
import DateFnsUtils from '@date-io/date-fns';
const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
    DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => <ArrowUpward {...props} ref={ref} />),
    ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
  };

class MaterialTableDemo extends React.Component {
  constructor(props) {
    var currDate=new Date()
    super(props);
this.state={
  fromDate:this.props.match.params.fromDate?Number(this.props.match.params.fromDate):currDate.getTime()-(24*60*60*1000*90),
  toDate:this.props.match.params.toDate?Number(this.props.match.params.toDate):currDate.getTime(),
  loading:true,
  filter:{},
  add:false,
  type:this.props.match.params.type,
  columns: [
    { title: 'Order-Id', field: 'orderId',editable: 'never' ,cellStyle: {
              fontSize:'12px'
            }},
    { title: 'User-Id', field: 'userId' ,editable: 'never', cellStyle: {
              fontSize:'10px'
            }},
    { title: 'Shipping Address', field: 'address',editable: 'never' ,cellStyle: {
              fontSize:'12px'
            }},
    { title: 'Amount', field: 'amount', type: 'numeric',editable: 'never',cellStyle: {
              fontSize:'12px'
            }},
    { title: 'mobile', field: 'mobile',editable: 'never',cellStyle: {
              fontSize:'12px'
            } },
    { title: 'Customer name', field: 'name', editable: 'never',cellStyle: {
              fontSize:'12px'
            } },
    { title: 'Order-Date', field: 'orderDate',editable: 'never' ,cellStyle: {
              fontSize:'12px'
            }},
  { title: 'Status', field: 'status' ,cellStyle: {
            fontSize:'12px'
          },
          render: rowData =>  <FormControl >
        <InputLabel htmlFor="age-simple">Status</InputLabel>
        <Select
          value={rowData.status}

          inputProps={{
            name: 'age',
            id: 'age-simple',
          }}
        >
          <MenuItem value={'seen'}>seen</MenuItem>
          <MenuItem value={'processing'}>processing</MenuItem>
          <MenuItem value={'shipped'}>shipped</MenuItem>
        </Select>
      </FormControl>
        },
  ],
  data: [


  ],

}
  }

  handleDateChangeF=(date)=>{
    this.setState({fromDate:date.getTime()})
    this.props.history.push("/orders/"+this.state.type+"/"+this.state.fromDate+"/"+this.state.toDate)
    this.componentDidMount()
  }

  handleDateChangeT=(date)=>{
    this.props.history.push("/orders/"+this.state.type+"/"+this.state.fromDate+"/"+this.state.toDate)
    this.setState({toDate:date.getTime()})
    this.componentDidMount()
  }
changeTab=(value)=>{
 
  if(value=='canceled')
  { return {status:11}
    }
  if(value=='all')
 { return {}
  }
  if(value=='closed')
  {return {status:13}
  }
  if(value=='returned')
 {return {status:12}
   }
  if(value=='open')
{ return {status:{$gte:-1,$lte:10}}
}
}
  componentDidMount(){
    var filter={}
    var filter=this.changeTab(this.state.type)
    console.log(filter)
    this.setState({loading:true})
    console.log(this.state.fromDate)
    axios.post('https://green-planet.herokuapp.com/public/admin/getOrders',{fromDate:this.state.fromDate,toDate:this.state.toDate,filter:{...filter}}).then(res=>{
   if(res.data){
     console.log(res.data)
     this.setState({loading:false})
     var orderData=[]
     res.data.map(data=>{
      var address=data.address.address+', '+data.address.locality+', '+data.address.city+', '+data.address.state+' - '+data.address.postal
     var total=0
     data.products.map(product=>{total=total+(product.quantity*product.price)})

       orderData.push({orderId:data.txnId,userId:data.userId,address:address,amount:total,orderDate:data.createdAt,name:data.address.name,mobile:data.address.phone,status:'processing'})
     })
     console.log(orderData)
     this.setState({data:orderData})
   }

    })
  }

  
  componentWillReceiveProps(newProps){
    var params=newProps.match.params
    var old=this.props.match.params
console.log(params)
    if(params.type!=old.type ||params.toDate!=old.toDate||params.fromDate!=old.fromDate)
    {
      console.log("changing")
      if(params.fromDate)
      this.setState({type:params.type,fromDate:Number(params.fromDate),toDate:Number(params.toDate)},()=> this.componentDidMount())
      else
      this.setState({type:params.type},()=> this.componentDidMount())

     
    }

  }


render() {


  return (
    <React.Fragment>
      <HeaderSecondary title="Orders"/>
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
      <Tabs  filter={this.state.type} filter={this.state.type} state={{...this.state}}/>
      <Grid container
        direction="row"
 justify="center"
 alignItems="center"
 xs={12}
 style={{paddingTop:'20px'}}
 spacing={2}
>
<Grid item lg={3} >
  <DateTimePicker
        label="From Date"
        inputVariant="outlined"
        value={this.state.fromDate}
        onChange={this.handleDateChangeF}
      /></Grid>
<Grid item lg={3} >
  <DateTimePicker
          label="To Date"
          inputVariant="outlined"
          value={this.state.toDate}
          onChange={this.handleDateChangeT}
        />
</Grid>
<Grid item lg={3} ><Button variant='contained' color='primary' onClick={()=>this.componentDidMount()}>Fetch Orders</Button></Grid>
<Grid item xs={12} >

  <MaterialTable
      style={{boxShadow:'none',width:'inherit'}}
      icons={tableIcons}
      isLoading={this.state.loading}
      options={{
       exportButton: true,
       exportAllData:true

     }}

        title={this.state.type}
        columns={this.state.columns}
        data={this.state.data}

        detailPanel={(rowData ,togglePanel)=> {

        return (
          <Container>
        <Details product={rowData.orderId} mode={2} handleToggle={(e)=>{console.log('toggle');togglePanel()}
        }/>

          </Container>

        )
        }}
        onRowClick={(event, rowData, togglePanel) => togglePanel()}

      />
</Grid>

  </Grid>
</MuiPickersUtilsProvider>

    </React.Fragment>
  );
}

}





export default connect()(MaterialTableDemo);
