import React,{Component} from 'react';
import Typography from '@material-ui/core/Typography'
import Title from './Title';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import MenuItem from '@material-ui/core/MenuItem';
import {UploadEditorImg} from '../utilities/utilFun'
import {updateAttachment,updateProduct,snackbar} from './../actions'
import {connect} from 'react-redux';
import {store} from '../App.js'
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab';
import axios from 'axios'
import  Hidden from '@material-ui/core/Hidden'
import Skeleton from '@material-ui/lab/Skeleton';
import Switch from '@material-ui/core/Switch'
import Shipping from '../components/shipping/shippngTab'
import FormControlLabel from '@material-ui/core/FormControlLabel';
import MediumEditor from 'medium-editor'
import BorderWrapper from 'react-border-wrapper'
import Spec from './spec'
import Divider from '@material-ui/core/Divider'
import FormHelperText from '@material-ui/core/FormHelperText'
import Chipset from './category/chipset'
import {

    withRouter
  } from "react-router-dom";
import { stat } from 'fs';
class CreateProduct extends Component {
  constructor(props) {
    super(props);

    this.state={
      text:'',
      editor:null,
      error:{
        title:false,
        price:false,
        sku:false,

      },
      valid:true,

    }

    this.waitf=this.waitf.bind(this)
    this.handleProcessed=this.handleProcessed.bind(this)
    this.handleAttach=this.handleAttach.bind(this)
this.deleteImg=this.deleteImg.bind(this)
this.onCreateProduct=this.onCreateProduct.bind(this)
  }

deleteImg=(index)=>{
  console.log(index)

  var arr=[...this.props.attachment]
  arr.splice(index,1)
  this.props.updateAttachment(arr)
}

handleField=(e)=>{
  var newObj={...this.props.products,[e.target.id]:e.target.value}
  console.log(newObj)
  this.props.updateProduct(newObj)

}

handleCat=(e)=>{
  var newObj={...this.props.products,category:e.target.value}
  console.log(newObj)
  this.props.updateProduct(newObj)
}
handleHSN=(e)=>{
  var newObj={...this.props.products,HSN:e.target.value}
  console.log(newObj)
  this.props.updateProduct(newObj)
}

 handleError=(keys,values,data)=>{
   var valid
var error=this.state.error
  keys.map(key=>{
    if(values[key]=='' || values[key]==null||values[key]==[])
  error[key]=true
    else
    {error[key]=false
    valid=false}
  })
this.setState({error:error,valid:valid},()=>this.createProduct(data))
 

 }

 createProduct=(data)=>{
  if(!this.state.valid)
  {
    this.props.snackbar({sType:'error',open:true,message:'Please fill all required fields!'})
 
  }
  if(this.state.valid){
    this.props.snackbar({sType:'info',open:true,message:'Adding '+data.title})
    axios.post('https://green-planet.herokuapp.com/public/product/add',{...data}).then(res=>{
      console.log(res.data)
      if (res.data){
      this.props.updateAttachment([])
      this.props.updateProduct({title:null,
      _id:null,
      description:null,
      category:null,
      price:null,
      priceVLE:null,
      quantity:null,
      sku:null})
      this.props.snackbar({sType:'success',open:true,message:'Successfuly added '+ res.data.title})
      this.props.history.push('/products/all')
      }
    }).catch(e=>{
        this.props.snackbar({sType:'error',open:true,message:'Opps something went wrong!'})
  
    })
  }
 
 }
onCreateProduct=(e)=>{
  var products=this.props.products


console.log(this.state)
  
var data={
  title:products.title,
  isPublished:products.isPublished,
  isFeatured:products.isFeatured,
  description:products.description,
  pricing:{
    price:products.price,
    priceVLE:products.priceVLE,
    MRP:products.MRP
  },
  tax:{
    CGST:products.CGST,
    SGST:products.SGST,
    IGST:products.IGST
  },

  sku:products.sku,
  HSN:products.HSN,
  quantity:products.quantity,
   specifiaction:products.specification,
 category:products.category,
 images:[],
 shipping:[],
 details:this.state.text
}

this.props.attachment.map((image)=>{
  data.images.push(image.url)
})
products.shipping.map((shp)=>{
  data.shipping.push({from:shp.from,to:shp.to,priceInter:shp.priceInter,priceIntra:shp.priceIntra})
})
console.log(data)
this.handleError(['title','price','priceVLE','quantity','HSN','sku','MRP','images'],products,data)




}

  handleAttach=(e)=>{

        var file=e.target.files[0]
          var type=file.name.split(".")

          console.log(file)
          var id=Date.now()
          var arr=[]
         arr=  [...this.props.attachment]
          var arrele={
          id:id,
          status:'queue',
          file:file,
          name:file.name,
          url:'',
          type:type.pop(),
          mime:file.type,
          }
          arr.push({...arrele})

          this.props.updateAttachment(arr)
          console.log(this.props.attachment)
      }

      handleProcessed(resolve){
console.log(resolve)
      var arr=[...this.props.attachment]
      var indexUP=arr.findIndex(
          item => item.id === resolve.id
      );
      arr[indexUP].status='processed';
      arr[indexUP].url=resolve.data.data.Location;
      console.log( arr[indexUP])

      this.props.updateAttachment(arr);
      console.log('processed')
    }




        waitf(value){
console.log('waitif')

    return new Promise(function (resolve, reject){
      UploadEditorImg(value.file,{ext:value.type,mime:value.mime})
      .then((data)=>resolve({id:value.id,data}))



    })
        }
        createSpec=()=>{
        var index=0
          var spec=[...this.props.products.specification]
        spec.splice(index+1,0,{title:'',values:[{key:'',value:''}]})

          this.props.updateProduct({specification:spec})
          console.log(this.props.products.specification);
        }

        componentDidMount(){



          this.state.editor = new MediumEditor(/*dom, */".medium-editable",{
                          autoLink: true,
                          imageDragging:true,
                          delay: 1000,
                          targetBlank: true,
                          toolbar: {
                              buttons: [
                                'bold',
                                'italic',
                                'quote',
                                'underline',
                                'anchor',
                                'h1',
                                'h2',
                                'h3',
                                'h4',
                                'h5',
                                'h6',
                                'orderedlist',

                              'unorderedlist',
                                'strikethrough',
                                'subscript',
                                'superscript',
                                'pre',
                                'image',
                                'html',
                                'justifyCenter'
                              ],
                              static: true,
                              sticky:true,
                              diffLeft: 25,
                              diffTop: 10,
                          },
                          anchor: {
                              placeholderText: 'Type a link',
                              customClassOption: 'btn',
                              customClassOptionText: 'Create Button'
                          },
                          paste: {
                              cleanPastedHTML: true,
                              cleanAttrs: ['style', 'dir'],
                              cleanTags: ['label', 'meta'],
                              unwrapTags: ['sub', 'sup']
                          },
                          anchorPreview: {
                              hideDelay: 300
                          },
                          placeholder: {
                              text: 'Tell your story...'
                          }
                        /*
                        placeholder: { text: "Tell your Story ...", hideOnClick: true },
                        toolbar: {
                          buttons: ['bold', 'italic']
                        } */
                      })





                      this.state.editor.subscribe('editableInput', (ev, editable) => {
                        if(typeof document !== 'undefined')

                          this.setState({

                            text: this.state.editor.getContent(0),

                          })


                      })









                      this.props.updateProduct({title:null,
                        _id:null,
                        description:null,
                        category:[],
                        isPublished:false,
                        isFeatured:false,
                        price:null,
                        priceVLE:null,
                        quantity:null,
                        sku:null,
                        CGST:0,
                        SGST:0,
                        IGST:0,
                        MRP:0,
                        specification:[],
                        shipping:[]
                      })
        }


render(){

  var data=[]
  console.log(this.props)
var arr=this.props.attachment
arr.map((value)=>{
  console.log(value)
  if(value.status=='queue'){
    console.log('queue')
      this.waitf(value).then((resolve)=>{this.handleProcessed(resolve)})
      value.status='processing'
}
data.push(value)
})

  return (
    <div style={{padding:'20px'}}>
    <React.Fragment>


      <form >
      <Grid container spacing={3} justify={'center'} >
      <Grid item xs={4}><Title>{this.props.mode==2?'Edit Product':'Add Product'}</Title></Grid>
<Grid item xs={4}>
  <FormControlLabel
         control={
           <Switch
             onChange={(e)=>{this.props.updateProduct({isPublished:e.target.checked})}}
                  checked={this.props.products.isPublished}

             value="checkedA"
             inputProps={{ 'aria-label': 'Publish' }}
           />
         }
         label="Publish"
       />



    </Grid>
    <Grid item xs={4}>
      <FormControlLabel
             control={
               <Switch
                onChange={(e)=>{this.props.updateProduct({isFeatured:e.target.checked})}}
                     checked={this.props.products.isFeatured}
                 value="checkedA"
                 inputProps={{ 'aria-label': 'Publish' }}
               />
             }
             label="Featured"
           />



        </Grid>

      <Grid item xs={12}><TextField
      id="title"
      fullWidth
     error= {this.state.error.title}
      InputLabelProps={{ shrink: true }}
      value={this.props.products.title}
      label="Title of product"
      helperText="Required"
      margin="normal"
      onChange={this.handleField}
      variant="outlined"
      ></TextField></Grid>



    <Grid item xs={6} md={4} lg={4}><TextField
      id="sku"
      fullWidth
      error= {this.state.error.sku}
      InputLabelProps={{ shrink: true }}
      value={this.props.products.sku}
      label="SKU of product"
      margin="normal"
      helperText="Required"
      variant="outlined"
      onChange={this.handleField}
      ></TextField></Grid>


    <Grid item xs={6} md={4} lg={4}>
      <TextField
      id="quantity"
      error= {this.state.error.quantity}
      fullWidth
      InputLabelProps={{ shrink: true }}
      label="Quantity Available"
      margin="normal"
      helperText="Required"
      onChange={this.handleField}
      type='number'
      value={this.props.products.quantity}
      variant="outlined"
      ></TextField>
      </Grid>
      <Grid item xs={6} md={4} lg={4}>
        <FormControl margin='normal'  variant="outlined"  helperText="Required"  error= {this.state.error.HSN}   id= 'HSN' fullWidth>
        <InputLabel  InputLabelProps={{ shrink: true }} shrink id="demo-simple-select-placeholder-label-label">
         HSN
        </InputLabel>
               <Select
                     variant="outlined"
                     id="demo-simple-select-placeholder-label-label"
                 margin="normal"
                    id= 'HSN'
                    helperText="Required"
              value={this.props.products?this.props.products.HSN:""}
               onChange={this.handleHSN}
      
                 inputProps={{
                   name: 'HSN',

                 }}
               >
               {this.props.config.tax.map((item)=>{
              return(    <MenuItem value={item.HSN}>
                {item.HSN}
              </MenuItem>)

               })}

               </Select>
               <FormHelperText>Required</FormHelperText>
               </FormControl>
             </Grid >


             <Grid item xs={12} style={{border:'1px dotted grey',borderRadius:'5px',marginBottom:'20px'}}>


             <FormControl margin='normal' variant="outlined"  error= {this.state.error.category}   id= 'category' fullWidth>
                    <InputLabel shrink htmlFor="age-helper" margin='normal'>category</InputLabel>
      <br/>
                 <Chipset setChecked={(checked)=>{
                     this.props.updateProduct({category:checked})
                   }} checked={this.props.products.category} config={this.props.config.category}/>
                    <FormHelperText>Required</FormHelperText>
                    </FormControl>

             </Grid>


      <Grid item xs={12}  style={{border:'1px dotted grey',borderRadius:'5px'}}>
      <FormControl error={this.state.error.images} margin='normal' variant="outlined"    id= 'category' fullWidth>
       <InputLabel shrink htmlFor="age-helper" margin='normal'>Add Images</InputLabel>
      <Grid container spacing={3} justify='left'>
        {
          this.props.attachment.map((value,index)=>{
            if(value.status=='processed')
             return(<Grid item  >
            <div style={{width:'100%',height:'100%',position:'relative',padding:'30px',border:'1px solid #e0e0e0',borderRadius:'8px'}}>
              <img src={value.url} style={{height:'200px',width:'auto'}}/>
              <IconButton onClick={()=>this.deleteImg(index)} aria-label="delete" color='secondary' style={{position:'absolute',right:0,top:0}}>
                        <DeleteIcon fontSize="small" />
                      </IconButton>
            </div>
              </Grid>
)

else{

  return(
    <Grid item  >
   <div style={{width:'100%',height:'100%',position:'relative',padding:'30px',border:'1px solid #e0e0e0',borderRadius:'8px'}}>
     <Skeleton height="200px" width={"150px"} />
     <IconButton onClick={()=>this.deleteImg(index)} aria-label="delete" color='secondary' style={{position:'absolute',right:0,top:0}}>
               <DeleteIcon fontSize="small" />
             </IconButton>
   </div>
     </Grid>
  )
}
          })

        }


        <Grid item xs={6} lg={3} style={{width:'200px',height:'200px'}} justify='center'>
        <center>  <Fab color="primary" aria-label="add" style={{marginTop:'70px'}} onClick={()=>document.getElementById('addImage').click()}>
      <AddIcon /> </Fab></center>
        </Grid>
      </Grid>
      <FormHelperText>Required</FormHelperText>
      </FormControl>
      </Grid>

      <Grid item xs={12}>
      <TextField
       id="description"
       InputLabelProps={{ shrink: true }}
       label="description"
       value={this.props.products.description}
       multiline
       onChange={this.handleField}
       rows="4"
       defaultValue="Describe your product here"
        fullWidth
       margin="normal"
       variant="outlined"
     />
      </Grid>

      <Grid item xs={6} lg={3}>
      <TextField
      id="MRP"
      fullWidth
      InputLabelProps={{ shrink: true }}
      label="MRP of product"
      error={this.state.error.MRP}
      margin="normal"
      helperText="Required"
      onChange={this.handleField}
      type='number'
      value={this.props.products.MRP}
      variant="outlined"
      ></TextField>
      </Grid>

      <Grid item xs={6} lg={3}>
      <TextField
      id="price"
      error={this.state.error.price}
      helperText="Required"
      fullWidth
      value={this.props.products.price}
      onChange={this.handleField}
      label="Price of product"
      margin="normal"
      InputLabelProps={{ shrink: true }}
      type='number'
      variant="outlined"
      ></TextField>
      </Grid>


      <Grid item xs={6} lg={3}>
      <TextField
      id="priceVLE"
      error={this.state.error.priceVLE}
      helperText="Required"
      fullWidth
      type='number'
      InputLabelProps={{ shrink: true }}
      value={this.props.products.priceVLE}
      onChange={this.handleField}
      label="Price for VLE"
      margin="normal"
      variant="outlined"
      ></TextField>
      </Grid>

      <Grid item xs={6} lg={3}>
      <TextField
      id="dShipping"
      helperText="Required"
      fullWidth
      type='number'
      InputLabelProps={{ shrink: true }}
      value={this.props.products.priceVLE}
      onChange={this.handleField}
      label="Default shipping charges"
      margin="normal"
      variant="outlined"
      ></TextField>
      </Grid>

      <Grid item xs={12} style={{border:'1px dotted grey',borderRadius:'5px'}}>
      <InputLabel shrink htmlFor="age-helper" margin='normal'>Product specifications</InputLabel>
        <Grid item xs={12}>
         <br/>
          {this.props.products.specification.length==0&&<center><Button
          variant='outlined'  onClick={this.createSpec}>
           Create Product Specification
            </Button>
            <br/></center>}
        </Grid>
      <Spec/>
      </Grid>
   
      <Grid item xs={12} style={{border:'1px dotted grey',marginTop:'20px',borderRadius:'5px',marginBottom:'20px'}}>


<FormControl margin='normal' variant="outlined"  fullWidth>
       <InputLabel shrink htmlFor="age-helper" margin='normal'>Describe your product</InputLabel>


      <textarea id="medium-editable" className="medium-editable editText" style={{minHeight:'400px',width:'100%',border:'none',outline: 'none'}} value={this.state.text} ></textarea>

      </FormControl>
      </Grid>
<Grid item xs={12}>
<Shipping/>
</Grid>
<Grid item xs={12}>
<Button variant='contained' fullWidth color='primary' onClick={this.onCreateProduct} style={{padding:"15px"}}><AddIcon style={{marginRight:'15px'}}/>{this.props.mode==2?'Update Product':'Create Product'}</Button>
</Grid>



      </Grid>
      </form>
<Hidden ><input style={{visibility:'hidden'}} type='file' id='addImage'  ref="Upload" onChange={this.handleAttach} /></Hidden>

    </React.Fragment>
    </div>
  );
}

}


const mapStateToProps = state => {

    return {
      attachment:state.product.attachment,
      products:state.product.products,
      config:state.common.config
     }
};

export default withRouter(connect(mapStateToProps,{updateAttachment,updateProduct,snackbar})(CreateProduct));
