import React,{Component} from 'react';

import Title from './Title';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import MenuItem from '@material-ui/core/MenuItem';
import {UploadEditorImg} from '../utilities/utilFun'
import {updateAttachment,updateProduct,snackbar} from './../actions'
import {connect} from 'react-redux';
import {store} from '../App.js'
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab';
import axios from 'axios'
import  Hidden from '@material-ui/core/Hidden'
import Skeleton from '@material-ui/lab/Skeleton';
import Switch from '@material-ui/core/Switch'
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Loader from './loader'
import Paper from '@material-ui/core/Paper';
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Stepper from './order/stepper'
import Dia from './order/dia'
import Chip from '@material-ui/core/Chip';
import Badge from '@material-ui/core/Badge';
import {

    withRouter
  } from "react-router-dom";
import { Typography } from '@material-ui/core';
class CreateProduct extends Component {
  constructor(props) {
    super(props);
this.state={
  Total:0,
  tax:0,
  shipping:0,
  subtotal:0,
  dataOrder:[],
  order:null,
  loader:true,
  dia:{open:false,message:'',value:0}
}
  }


        componentDidMount(){
this.setState({loader:true})
          var address=''
          axios.post('https://green-planet.herokuapp.com/public/admin/getOrders',{txnId:this.props.product}).then(res=>{
         if(res.data){
  console.log(res.data)
           const products=res.data[0].products
           var dataOrder=[]
           this.setState({loader:false})
           let Subtotal=0
           let price=0
           let tax=0
           let shipping=0
           products.map((data,i)=>{
             var rp=data.price/(1+(data.CGST+data.IGST+data.SGST)/100)
             Subtotal=Subtotal+(data.price*data.quantity)+(data.shipping*data.quantity)
             shipping=shipping+(data.shipping*data.quantity)
             price=price+rp
             tax=tax+(data.price-rp)
             dataOrder.push({image:data.product.images[0],title:data.product.title,sku:data.product.sku,quantity:data.quantity,shipping:data.shipping,tax:data.CGST+data.IGST+data.SGST,price:data.price})
           })
this.setState({Total:Subtotal,tax,shipping,subtotal:price,dataOrder,order:res.data[0]})

         }




         })

         }
         handleAction=(value)=>{
          axios.post('https://green-planet.herokuapp.com/public/admin/updateOrder',{_id:this.state.order._id,status:value}).then(res=>{

            this.setState({dia:{open:false,message:'',value:0},order:{...this.state.order,status:value}})
         
          })
         }

render(){


  return (
    <React.Fragment style={{backgroundColor:'grey'}}>
    {this.state.loader&&<Loader/>}
    <Dia open={this.state.dia.open} value={this.state.dia.value} message={this.state.dia.message} handleClose={()=>this.setState({dia:{open:false}})} handleConfirm={this.handleAction}/>
      {!this.state.loader&&<Grid container spacing={3} justify={'center'} style={{marginTop:'50px',marginBottom:'50px'}}>
      <Grid item xs={6}><Title>{'Order Details'}</Title></Grid>
      <Grid item xs={6}>   {this.state.order&&this.state.order.status==13&&<Chip label={'closed'}  color="secondary"/>}
      {this.state.order&&this.state.order.status==12&&<Chip label={'Returned'}  color="secondary"/>}
      {this.state.order&&this.state.order.status==11&&<Chip label={'canceled'}  color="secondary"/>}
      {<Chip label={'Important'}  color="primary"/>}
      </Grid>
   
      <Grid item xs={12}>
      <Paper >
            <Table >
              <TableHead>
                <TableRow>
                  <TableCell>Product</TableCell>
                  <TableCell>Name</TableCell>
                  <TableCell >SKU</TableCell>
                        <TableCell >Qty.</TableCell>
                  <TableCell >Shipping</TableCell>
                  <TableCell >Price</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {this.state.dataOrder.map(data=>(

                  <TableRow key={data.sku}>
                    <TableCell>
                    <img src={data.image} style={{height:'100px',width:'auto'}}/>
                  
                   
                    </TableCell>
                    <TableCell>{data.title}
                      </TableCell>
                    <TableCell >{data.sku}</TableCell>
                    <TableCell >{data.quantity}</TableCell>
                      <TableCell >{data.shipping}</TableCell>
                    <TableCell >{data.price}</TableCell>
                  </TableRow>
                ))}




              </TableBody>
            </Table>
          </Paper></Grid>
        <Grid xs={12} md={6} lg={6}>
          <List dense>

                       <ListItem>
                         <ListItemText
                           primary="Shipping Address"
                           secondary={this.state.order?this.state.order.address.address:null}
                         />
                       </ListItem>
                       <ListItem>
                         <ListItemText
                           primary="Seller Address"
                           secondary={this.state.order?this.state.order.seller.address:null}
                         />
                       </ListItem>
                       <ListItem>
                         <ListItemText
                           primary={"Tax :"+this.state.tax.toFixed(2)}
                           secondary={'CGST + SGST +IGST'}
                         />
                       </ListItem>
                       <ListItem>
                         <ListItemText
                           primary={"Shipping Charges :"+this.state.shipping}

                         />
                       </ListItem>,
                       <ListItem>
                         <ListItemText
                           primary={"Total Amount: "+this.state.Total}
                           secondary={'inclusive of all taxes'}
                         />
                       </ListItem>

                   </List>
                   
        </Grid>
        <Grid item md={6} xs={12} lg={6}>
        {this.state.order&&<Stepper data={this.state.order}/>}
      
        </Grid>
        <Grid container spacing={2}>
        <Grid item xs={12}>
        <Typography variant={'caption'}>Actions</Typography>
        </Grid>
        <Grid item xs={2}> <Button variant='contained' color="primary" fullWidth>Download Invoice</Button></Grid> 
        <Grid item xs={2}> <Button variant='contained' color="primary" fullWidth onClick={()=>this.setState({dia:{open:true,message:"Are you sure want to mark order as canceled.This process is irreversible please confirm to continue",value:11}})}>Cancel Order</Button></Grid>
        <Grid item xs={2}> <Button variant='contained'  color="primary" fullWidth onClick={()=>this.setState({dia:{open:true,message:"Are you sure want to mark order as Returned.This process is irreversible please confirm to continue",value:12}})}>Return Order</Button></Grid>
        <Grid item xs={2}> <Button variant='contained'   color="primary" fullWidth onClick={()=>this.setState({dia:{open:true,message:"Are you sure want to mark order as closed.This process is irreversible please confirm to continue",value:13}})}>Close Order</Button></Grid>
        <Grid item xs={2}> <Button variant='contained'  color="primary" fullWidth>mark as Imortant</Button></Grid>
        </Grid>
        </Grid>}
    </React.Fragment>

)

}
}


const mapStateToProps = state => {

    return {
      attachment:state.product.attachment,
      products:state.product.products,
      config:state.common.config
     }
};

export default withRouter(connect(mapStateToProps,{updateAttachment,updateProduct,snackbar})(CreateProduct));
