import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import HelpIcon from '@material-ui/icons/Help';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import Link from '@material-ui/core/Link';
import MenuIcon from '@material-ui/icons/Menu';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Tab from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';
import Toolbar from '@material-ui/core/Toolbar';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {toggleDrawer,logout} from './actions/index'
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
const lightColor = 'rgba(255, 255, 255, 0.7)';

const styles = theme => ({
  secondaryBar: {
    zIndex: 0,
  },
  menuButton: {
    marginLeft: -theme.spacing(1),
  },
  iconButtonAvatar: {
    padding: 4,
  },
  link: {
    textDecoration: 'none',
    color: lightColor,
    '&:hover': {
      color: theme.palette.common.white,
    },
  },
  button: {
    borderColor: lightColor,
  },
});

function Header(props) {
  const { classes, onDrawerToggle } = props;
  console.log('header data')
console.log(props)
  return (
    <React.Fragment>
      <AppBar color="primary" position="sticky" elevation={0}>
        <Toolbar>
          <Grid container spacing={1} alignItems="center">
           
       {       !props.drawer &&<Grid item>
                <IconButton
                  color="inherit"
                  aria-label="open drawer"
                  onClick={props.toggleDrawer}
                  className={classes.menuButton}
                >
                  <MenuIcon />
                </IconButton>
              </Grid>}
              <Grid item><img src="https://vindercomm.vilabs.tech/assets/logo.png" style={{width:'auto',height:'40px'}}/></Grid>
          
            <Grid item xs />
            <Grid item>
              <Link className={classes.link} href="#" variant="body2">
                Go to doc
              </Link>
            </Grid>
            <Grid item>
              <Tooltip title="Alerts • No alerts">
                <IconButton color="inherit">
                  <NotificationsIcon />
                </IconButton>
              </Tooltip>
            </Grid>
            <Grid item>
              <IconButton color="inherit" className={classes.iconButtonAvatar}>
              <IconButton><AccountCircleIcon style={{color:'white'}}/></IconButton>
              </IconButton>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
     
    </React.Fragment>
  );
}

Header.propTypes = {
  classes: PropTypes.object.isRequired,
  onDrawerToggle: PropTypes.func.isRequired,
};

const mapStateToProps = state => {
  console.log(state.product.attachment)
      return {
        attachment:state.product.attachment,
        drawer:state.common.drawer
       }
  };
  

export default withRouter(withStyles(styles)(connect(mapStateToProps,{toggleDrawer,logout}) (Header)));