import React from 'react';
import MaterialTable from 'material-table';
import { forwardRef } from 'react';
import {updateAttachment,updateProduct,snackbar} from '../../actions'
import {connect} from 'react-redux';
import AddBox from '@material-ui/icons/AddBox';
import ArrowUpward from '@material-ui/icons/ArrowUpward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import axios from 'axios';
import {

    withRouter
  } from "react-router-dom";
const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
    DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => <ArrowUpward {...props} ref={ref} />),
    ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
  };

class MaterialTableDemo extends React.Component {
  constructor(props) {
    super(props);
this.state={
  add:false,
  columns: [
    { title: 'From(no of items)', field: 'from' ,type: 'numeric'},
    { title: 'to (no of items)', field: 'to',type: 'numeric' },
    { title: 'Price InterState', field: 'priceInter', type: 'numeric' },
    { title: 'Price IntraState', field: 'priceIntra', type: 'numeric' },

  ],
  data: [
{from:1,to:10,priceInter:12,priceIntra:23}

  ],

}
  }

  componentDidMount(){

  }
  checkValidity(obj){
    var validity=false
    var data=this.props.shipping
    console.log(obj)
    if(Number(data.length)<1)
    return true
    if(Number(obj.from)<1||Number(obj.to)<1)
    return false
    if(Number(obj.from)>Number(obj.to))
      return false
for(var i=0;i<data.length;i++){

  if((Number(obj.from)>Number(data[i].to)||Number(obj.to) <Number(data[i].from))){
console.log('valid')
    validity=true

    }
    else{
      validity=false
      break
    }

}
console.log(validity)
return validity

  }



render() {


  return (
    <React.Fragment>

<MaterialTable
    style={{width:'100%',boxShadow:'none'}}
    icons={tableIcons}
    options={{
     exportButton: true
   }}
    size='large'
      title="Shipping Rules"
      columns={this.state.columns}
      data={this.props.shipping}
      editable={{
        onRowAdd: newData =>
                new Promise((resolve, reject) => {
                  setTimeout(() => {
                    {
                  let data = this.props.shipping;
                      if(this.checkValidity(newData)){
                        data.push({from:Number(newData.from),to:Number(newData.to),priceIntra:Number(newData.priceIntra),priceInter:Number(newData.priceInter)});
                      this.props.updateProduct({shipping:data});
                      }

                    }
                    resolve()
                  }, 1000)
                }),
              onRowUpdate: (newData, oldData) =>
                new Promise((resolve, reject) => {
                  setTimeout(() => {
                    {
                        let data = this.props.shipping;
                      const index = data.indexOf(oldData);
                      data[index] = {from:Number(newData.from),to:Number(newData.to),priceInter:Number(newData.priceInter),priceIntra:Number(newData.priceIntra)};
                    this.props.updateProduct({shipping:data});
                    }
                    resolve()
                  }, 1000)
                }),
              onRowDelete: oldData =>
                new Promise((resolve, reject) => {
                  setTimeout(() => {
                    {
                      let data = this.props.shipping;
                      const index = data.indexOf(oldData);
                      data.splice(index, 1);
                      this.props.updateProduct({shipping:data});
                    }
                    resolve()
                  }, 1000)
                }),
      }}


    />
    </React.Fragment>
  );
}

}
const mapStateToProps = state => {

    return {
      attachment:state.product.attachment,
      products:state.product.products,
      shipping:state.product.products.shipping
     }
};

export default withRouter(connect(mapStateToProps,{updateAttachment,updateProduct,snackbar})(MaterialTableDemo));
