import React from 'react';
import PropTypes from 'prop-types';
import { createMuiTheme, ThemeProvider, withStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Hidden from '@material-ui/core/Hidden';
import Typography from '@material-ui/core/Typography';
import Link from '@material-ui/core/Link';
import Navigator from './Navigator';
import Content from './Content';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import Header from './Header';
import Dashboard from './adminPages/Dashboard'
import {withRouter} from "react-router-dom";
import {connect} from 'react-redux';
import {BrowserRouter, Switch, Route, Redirect} from 'react-router-dom';
import {toggleDrawer} from './actions/index'
import SELLER from './adminPages/seller'
import Config from './adminPages/config/config'
import Tax from './adminPages/config/tax'
import Sstates from './adminPages/config/states'
import Customers from './adminPages/allUsers'
import Admin from './adminPages/Admins'
import VLE2 from './adminPages/VLE2'
import CategoryTree from './adminPages/category/catTree'
import products from "./adminPages/products"
import clsx from 'clsx';
import EditProduct from './adminPages/editProduct'
import AddProduct from './adminPages/createProduct';
import Orders from './adminPages/userOrders';
import './src/assets/css/material.css'
import Home from './adminPages/home1'
import SalesReport from './adminPages/reports/salesReport'
function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

let theme = createMuiTheme({
  palette: {
    primary: {
      light: '#63ccff',
      main: '#009be5',
      dark: '#006db3',
    },
  },
  typography: {
    h5: {
      fontWeight: 500,
      fontSize: 26,
      letterSpacing: 0.5,
    },
  },
  shape: {
    borderRadius: 8,
  },
  props: {
    MuiTab: {
      disableRipple: true,
    },
  },
  mixins: {
    toolbar: {
      minHeight: 48,
    },
  },
});

theme = {
  ...theme,
  overrides: {
    MuiDrawer: {
      paper: {
        backgroundColor: '#18202c',
      },
    },
    MuiButton: {
      label: {
        textTransform: 'none',
      },
      contained: {
        boxShadow: 'none',
        '&:active': {
          boxShadow: 'none',
        },
      },
    },
    MuiTabs: {
      root: {
        marginLeft: theme.spacing(1),
      },
      indicator: {
        height: 3,
        borderTopLeftRadius: 3,
        borderTopRightRadius: 3,
        backgroundColor: theme.palette.common.white,
      },
    },
    MuiTab: {
      root: {
        textTransform: 'none',
        margin: '0 16px',
        minWidth: 0,
        padding: 0,
        [theme.breakpoints.up('md')]: {
          padding: 0,
          minWidth: 0,
        },
      },
    },
    MuiIconButton: {
      root: {
        padding: theme.spacing(1),
      },
    },
    MuiTooltip: {
      tooltip: {
        borderRadius: 4,
      },
    },
    MuiDivider: {
      root: {
        backgroundColor: '#404854',
      },
    },
    MuiListItemText: {
      primary: {
        fontWeight: theme.typography.fontWeightMedium,
      },
    },
    MuiListItemIcon: {
      root: {
        color: 'inherit',
        marginRight: 0,
        '& svg': {
          fontSize: 20,
        },
      },
    },
    MuiAvatar: {
      root: {
        width: 32,
        height: 32,
      },
    },
  },
};

const drawerWidth = 220;

const styles = {
  root: {
    display: 'flex',
    minHeight: '100vh',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  app: {
    width: `calc(100% - ${drawerWidth}px)`,
    flexGrow: 1,
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),

    [theme.breakpoints.up('md')]: {
      marginLeft: drawerWidth,
    }
 
    
  },

  appShift: {
    width:'100%',
    flexGrow: 1,
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },

  main: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
    padding: theme.spacing(2),
   minHeight:"100vh",
    background: '#eaeff1',
    flexGrow: 1,
  
 
    
  },
  footer: {
    padding: theme.spacing(2),
    background: '#eaeff1',
  },
};

function Paperbase(props) {
  const { classes } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };
var dw=props.drawer?window.innerWidth-drawerWidth:window.innerWidth

  return (
    <ThemeProvider theme={theme}>
        <BrowserRouter>
      <div className={classes.root} >
        <CssBaseline />
        <Hidden mdUp>      <Navigator variant="temporary"/></Hidden>
     
            <Navigator variant="persistent"/>
             
        <div  className={clsx(classes.app, {
          [classes.appShift]: !props.drawer,
        })} >
          <Header onDrawerToggle={handleDrawerToggle} />
     
      

    


          <React.Fragment>
            <Switch>
              <Route exact path={'/'}
              component={Home}
              />

              <Route exact path={'/product'}
                component={AddProduct} />
              <Route exact path={'/EditProduct/:id'}
                  component={EditProduct} />
                <Route exact path={'/products/Add'}
                      component={AddProduct} />
              <Route exact path={'/orders/:type/:fromDate/:toDate'}
                  component={Orders} />
                  <Route exact path={'/orders/:type/'}
                  component={Orders} />
                <Route exact path={'/user/Customers'}
                      component={Customers} />

                    <Route exact path={'/products/Category'}
                            component={CategoryTree} />
                <Route exact path={'/products/all'} component={products}/>
                <Route exact path={'/user/admins'}
                      component={Admin} />
                    <Route exact path={'/user/VLE'}
                            component={VLE2} />
                          <Route exact path={'/user/seller'}
                                    component={SELLER} />

                                  <Route exact path={'/config/general'}
                                              component={Config} />
                                              <Route exact path={'/config/tax'}
                                              component={Tax} />
                                              <Route exact path={'/config/states'}
                                              component={Sstates} />
                                              <Route exact path={'/report/:type'}
                                              component={SalesReport} />

                                                 <Route exact path={'/report/:type/:fromDate/:toDate'}
                                              component={SalesReport} />

                                              <Route exact path={'/report/product'}
                                              component={Sstates} />
                                              <Route exact path={'/config/customer'}
                                              component={Sstates} />
            </Switch>
          </React.Fragment>



     

     
        
       
        </div>
      </div>
      

</BrowserRouter>
    </ThemeProvider>
  );
}

Paperbase.propTypes = {
  classes: PropTypes.object.isRequired,
};

const mapStateToProps = state => {
  console.log(state.product.attachment)
      return {
        attachment:state.product.attachment,
        drawer:state.common.drawer
       }
  };
  

export default withStyles(styles)(connect(mapStateToProps,{toggleDrawer}) (Paperbase));