
const defaultState = {
    appName: '',
    modalMode: false,
    snackbar:
    {open:false,
    sType:'success',
    message:'',}
    ,
    config:{
      category:[],
      tax:[]
    },
    drawer:window.innerWidth<800?false:true,
    loader:false,
  };

  export default (state = defaultState, action) => {
    switch (action.type) {


       case 'SNACKBAR':
       return{
         ...state,
         snackbar:action.snackbar

       }

       case 'ToggleLoader':
       return{
         ...state,
         loader:!state.loader,

       }


       case 'ToggleDrawer':
console.log('toggling')
       return{
         ...state,
         drawer:!state.drawer,

       }
       case 'SETCONFIG':
       return{
         ...state,
         config:{
...state.config,
...action.config
         }

       }

      default:
        return state;
    }
  };
