import React,{Component} from 'react';

import Title from './Title';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import TextField from '@material-ui/core/TextField';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import MenuItem from '@material-ui/core/MenuItem';
import {UploadEditorImg} from '../utilities/utilFun'
import {updateAttachment,updateProduct,snackbar} from './../actions'
import {connect} from 'react-redux';
import {store} from '../App.js'
import IconButton from '@material-ui/core/IconButton';
import AddIcon from '@material-ui/icons/Add';
import DeleteIcon from '@material-ui/icons/Delete';
import Button from '@material-ui/core/Button'
import Fab from '@material-ui/core/Fab';
import axios from 'axios'
import  Hidden from '@material-ui/core/Hidden'
import Skeleton from '@material-ui/lab/Skeleton';
import Switch from '@material-ui/core/Switch'
import Shipping from '../components/shipping/shippngTab'
import FormControlLabel from '@material-ui/core/FormControlLabel';
import ListItemText from '@material-ui/core/ListItemText'
import List from '@material-ui/core/List'
import Paper from '@material-ui/core/Paper'
import ListItem from '@material-ui/core/ListItem'
import ListSubheader from '@material-ui/core/ListSubheader'
import InputBase from '@material-ui/core/InputBase'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import {

    withRouter
  } from "react-router-dom";
class CreateProduct extends Component {
  constructor(props) {
    super(props);

    this.state={
      text:'',
      editor:null,

    }


  }
  handleTiltle=(e,index)=>{
      var spec=[...this.props.products.specification]
      spec[index].title=e.target.value
    this.props.updateProduct({specification:spec})
  }

  AddColumn=(index)=>{
    console.log(index)
    var spec=[...this.props.products.specification]
  spec.splice(index+1,0,{title:'',values:[{key:'',value:''}]})

    this.props.updateProduct({specification:spec})
    console.log(this.props.products.specification);
  }
  AddKV=(index,index2)=>{
    console.log(index)
    var spec=[...this.props.products.specification]
  spec[index].values.splice(index2+1,0,{key:'',value:''})

    this.props.updateProduct({specification:spec})
    console.log(this.props.products.specification);
  }
  DeleteKV=(index,index2)=>{
    if(index2==0){
      this.DeleteColumn(index)
    }
    else{
      console.log(index)
      var spec=[...this.props.products.specification]
    spec[index].values.splice(index2,1)

      this.props.updateProduct({specification:spec})
      console.log(this.props.products.specification);
    }

  }
  DeleteColumn=(index)=>{
    console.log(index)
    var spec=[...this.props.products.specification]
  spec.splice(index,1)

    this.props.updateProduct({specification:spec})
    console.log(this.props.products.specification);
  }

  handleValue=(e,i,j)=>{
    var spec=[...this.props.products.specification]
    spec[i].values[j].value=e.target.value
  this.props.updateProduct({specification:spec})
  }

handleKey=(e,i,j)=>{
  var spec=[...this.props.products.specification]
  spec[i].values[j].key=e.target.value
this.props.updateProduct({specification:spec})
}

render(){



  return (
    <React.Fragment>


<Paper>


<List disablePadding={true} dense={true}>
{this.props.products.specification.map((item,index)=>{
var itemData=[]


  itemData=item.values.map((item2,index2)=>{

    return(

      <ListItem dense divider >
      <Grid item xs={4} lg={3} >
      <ListItemText>
      <InputBase placeholder="Key" value={item2.key} onChange={(e)=>this.handleKey(e,index,index2)}/>
      </ListItemText>
      </Grid>
      <Grid item xs={8} lg={9}>

      <ListItemText>
      <InputBase placeholder="Value" value={item2.value} onChange={(e)=>this.handleValue(e,index,index2)}/>
      </ListItemText>
      <ListItemSecondaryAction>
                 <IconButton edge="end" aria-label="comments" onClick={()=>this.AddKV(index,index2)}>
                   <AddIcon/>
                 </IconButton>
                 <IconButton edge="end" aria-label="comments" onClick={()=>this.DeleteKV(index,index2)}>
                 <DeleteIcon/>
                 </IconButton>

               </ListItemSecondaryAction>
      </Grid>
    </ListItem>

    )
  })
  itemData.splice(0,0,

    <ListItem style={{borderBottom:'1px solid #f0f0f0'}}>
  <ListSubheader id={index}><InputBase id={index} style={{fontWeight:'bold',color:'grey'}} placeholder='Please enter title' value={item.title} onChange={(e)=>this.handleTiltle(e,index)}/></ListSubheader>
  <ListItemSecondaryAction>
             <IconButton edge="end" aria-label="comments" onClick={()=>this.AddColumn(index)}>
               <AddIcon/>
             </IconButton>
             <IconButton edge="end" aria-label="comments" onClick={()=>this.DeleteColumn(index)}>
             <DeleteIcon/>
             </IconButton>

           </ListItemSecondaryAction>


</ListItem>
  )
return(itemData)
})}


</List>
</Paper>
    </React.Fragment>
  );
}

}


const mapStateToProps = state => {

    return {
      attachment:state.product.attachment,
      products:state.product.products,
      config:state.common.config
     }
};

export default withRouter(connect(mapStateToProps,{updateAttachment,updateProduct,snackbar})(CreateProduct));
