import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import ViewWeekIcon from '@material-ui/icons/ViewWeek';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import NotificationsIcon from '@material-ui/icons/Notifications';
import {Link,withRouter} from 'react-router-dom';
import EditProduct from './editProduct'
import AddProduct from './createProduct';
import Deposits from './Deposits';
import Orders from './userOrders';
import {BrowserRouter, Switch, Route, Redirect} from 'react-router-dom';
import CategoryTree from './category/catTree'
import products from "./products"
import RecordVoiceOverIcon from '@material-ui/icons/RecordVoiceOver';
import VLE2 from './VLE2'
import {connect} from 'react-redux';
import Admin from './Admins'
import PowerSettingsNewIcon from '@material-ui/icons/PowerSettingsNew';
import Customers from './allUsers'
import AssignmentIcon from '@material-ui/icons/Assignment';
import SELLER from './seller'
import Config from './config/config'
import Tax from './config/tax'
import Sstates from './config/states'
import Home from '../src/views/Dashboard/Dashboard.js'
import ListNew from './listNew'
import Loader from './loader'
import AccountCircleIcon from '@material-ui/icons/AccountCircle';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}

        Green Planet
    {' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    backgroundColor:'#fff',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    height:'100vh',
    backgroundColor:'rgba(0, 0, 0, 0.87)',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(0),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(0),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
 
    backgroundColor:'#fff',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
    backgroundColor:'#fff'
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
}));

 function Dashboard(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

  return (
                <BrowserRouter>
  
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Grid container spacing={3}>


              <React.Fragment>
                <Switch>
                <Route exact path={'/home'}
                    component={Home} />
                  <Route exact path={'/product'}
                    component={Home} />
                  <Route exact path={'/EditProduct/:id'}
                      component={EditProduct} />
                    <Route exact path={'/AddProduct'}
                          component={AddProduct} />
                  <Route exact path={'/order'}
                      component={Orders} />
                    <Route exact path={'/Customers'}
                          component={Customers} />

                        <Route exact path={'/Category'}
                                component={CategoryTree} />
                    <Route exact path={'/products'} component={products}/>
                    <Route exact path={'/admins'}
                          component={Admin} />
                        <Route exact path={'/VLE'}
                                component={VLE2} />
                              <Route exact path={'/seller'}
                                        component={SELLER} />

                                      <Route exact path={'/config'}
                                                  component={Config} />
                                                  <Route exact path={'/config/tax'}
                                                  component={Tax} />
                                                  <Route exact path={'/config/states'}
                                                  component={Sstates} />
                </Switch>
              </React.Fragment>



          </Grid>
        </Container>
    
      </main>

  
    </BrowserRouter>
  );
}

const mapStateToProps = state => {
console.log(state.product.attachment)
    return {
      attachment:state.product.attachment,
      loading:state.common.loader
     }
};

export default connect(mapStateToProps,null)(Dashboard);
