import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import TreeView from '@material-ui/lab/TreeView';
import TreeItem from '@material-ui/lab/TreeItem';
import Typography from '@material-ui/core/Typography';
import MailIcon from '@material-ui/icons/Mail';
import DeleteIcon from '@material-ui/icons/Delete';
import Label from '@material-ui/icons/Label';
import SupervisorAccountIcon from '@material-ui/icons/SupervisorAccount';
import InfoIcon from '@material-ui/icons/Info';
import ForumIcon from '@material-ui/icons/Forum';
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
import {Link,withRouter} from 'react-router-dom';
import ListSubheader from '@material-ui/core/ListSubheader';
import DashboardIcon from '@material-ui/icons/Dashboard';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import PeopleIcon from '@material-ui/icons/People';
import BarChartIcon from '@material-ui/icons/BarChart';
import LayersIcon from '@material-ui/icons/Layers';
import RecordVoiceOverIcon from '@material-ui/icons/RecordVoiceOver';
import ViewWeekIcon from '@material-ui/icons/ViewWeek';
import MenuIcon from '@material-ui/icons/Menu';
import AddIcon from '@material-ui/icons/Add';
import SupervisedUserCircleIcon from '@material-ui/icons/SupervisedUserCircle';
const useTreeItemStyles = makeStyles(theme => ({
  root: {
    color:'white',
    '&:focus > $content': {
      backgroundColor: `var(--tree-view-bg-color, ${theme.palette.grey[400]})`,
      color: 'var(--tree-view-color)',
    },
  },
  content: {
    color:'white',
 
    borderTopRightRadius: theme.spacing(2),
    borderBottomRightRadius: theme.spacing(2),
    paddingRight: theme.spacing(1),
    fontWeight: theme.typography.fontWeightMedium,
    '$expanded > &': {
      fontWeight: theme.typography.fontWeightRegular,
    },
  },
  group: {
    marginLeft: 0,
    '& $content': {
      paddingLeft: theme.spacing(2),
    },
  },
  expanded: {},
  label: {
    fontWeight: 'inherit',
    color: 'inherit',
  },
  labelRoot: {
    display: 'flex',
    alignItems: 'center',
   
    padding: theme.spacing(2, 0),
  },
  labelIcon: {
    marginRight: theme.spacing(1),
  },
  labelText: {
    fontWeight: 'inherit',
    flexGrow: 1,
  },
}));

function StyledTreeItem(props) {
  const classes = useTreeItemStyles();
  const { labelText, labelIcon: LabelIcon, labelInfo, color, bgColor, ...other } = props;

  return (
    <TreeItem
      label={
        <div className={classes.labelRoot}>
          <LabelIcon color="inherit" className={classes.labelIcon} />
          <Typography variant="body" className={classes.labelText}>
            {labelText}
          </Typography>
          <Typography variant="caption" color="inherit">
            {labelInfo}
          </Typography>
        </div>
      }
      style={{
        '--tree-view-color': color,
        '--tree-view-bg-color': bgColor,
   
      }}
      classes={{
        root: classes.root,
        content: classes.content,
        expanded: classes.expanded,
        group: classes.group,
        label: classes.label,
      }}
      {...other}
    />
  );
}

StyledTreeItem.propTypes = {
  bgColor: PropTypes.string,
  color: PropTypes.string,
  labelIcon: PropTypes.elementType.isRequired,
  labelInfo: PropTypes.string,
  labelText: PropTypes.string.isRequired,
};

const useStyles = makeStyles({
  root: {
    height: 264,
    flexGrow: 1,
    maxWidth: 400,
  },
});

export default function GmailTreeView() {
  const classes = useStyles();

  return (
    <TreeView
      className={classes.root}
      defaultExpanded={['3']}
      defaultCollapseIcon={<ArrowDropDownIcon />}
      defaultExpandIcon={<ArrowRightIcon />}
      defaultEndIcon={<div style={{ width: 24 }} />}
    >
      <StyledTreeItem nodeId="1" labelText="Dashboard" labelIcon={DashboardIcon} />
      <Link to={'/order'} >   <StyledTreeItem nodeId="2" labelText="Orders" labelIcon={ShoppingCartIcon} /></Link>
            <Link to={'/order'} >   <StyledTreeItem nodeId="2" labelText="Cutomers" labelIcon={PeopleIcon} /></Link>
      <StyledTreeItem nodeId="3" labelText="Products" labelIcon={ViewWeekIcon}>
      <Link to={'/products'} >   <StyledTreeItem
          nodeId="5"
          labelText="All Products"
          labelIcon={SupervisorAccountIcon}
          labelInfo="90"
          color="#1a73e8"
          bgColor="#e8f0fe"
        /></Link>
    <Link to={'/addproduct'} >    <StyledTreeItem
          nodeId="6"
          labelText="Add new Product"
          labelIcon={AddIcon}
          labelInfo="2,294"
          color="#e3742f"
          bgColor="#fcefe3"
        /></Link>
      <Link to={'/category'} >    <StyledTreeItem
          nodeId="7"
          labelText="Category"
          labelIcon={Label}
          labelInfo="3,566"
          color="#a250f5"
          bgColor="#f3e8fd"
        /></Link>
        <StyledTreeItem
          nodeId="8"
          labelText="Promotions"
          labelIcon={LocalOfferIcon}
          labelInfo="733"
          color="#3c8039"
          bgColor="#e6f4ea"
        />
      </StyledTreeItem>
      <Link to={'/vle'} >   <StyledTreeItem nodeId="2" labelText="VLE" labelIcon={PeopleIcon} /></Link>
      <Link to={'/seller'} >   <StyledTreeItem nodeId="2" labelText="Sellers" labelIcon={PeopleIcon} /></Link>
      <Link to={'/admins'} >   <StyledTreeItem nodeId="2" labelText="Admins" labelIcon={PeopleIcon} /></Link>
          
          
            <StyledTreeItem nodeId="2" labelText="Configuration" labelIcon={RecordVoiceOverIcon} >
            <Link to={'/config/tax'} >   <StyledTreeItem nodeId="2" labelText="Taxation" labelIcon={RecordVoiceOverIcon} ></StyledTreeItem></Link>
            <Link to={'/config/states'} >   <StyledTreeItem nodeId="2" labelText="Serviceable States" labelIcon={RecordVoiceOverIcon} ></StyledTreeItem></Link>

             <Link to={'/config'} >   <StyledTreeItem nodeId="2" labelText="General" labelIcon={RecordVoiceOverIcon} ></StyledTreeItem></Link>

            
            </StyledTreeItem>
      <StyledTreeItem nodeId="4" labelText="Reports" labelIcon={Label} />
    </TreeView>
  );
}
