import React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@material-ui/core/AppBar';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import HelpIcon from '@material-ui/icons/Help';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import Link from '@material-ui/core/Link';
import MenuIcon from '@material-ui/icons/Menu';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Tab from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';
import Toolbar from '@material-ui/core/Toolbar';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import {toggleDrawer} from './actions/index'
const lightColor = 'rgba(255, 255, 255, 0.7)';

const styles = theme => ({
  secondaryBar: {
    zIndex: 0,
  },
  menuButton: {
    marginLeft: -theme.spacing(1),
  },
  iconButtonAvatar: {
    padding: 4,
  },
  link: {
    textDecoration: 'none',
    color: lightColor,
    '&:hover': {
      color: theme.palette.common.white,
    },
  },
  button: {
    borderColor: lightColor,
  },
});

function Header(props) {
  const { classes, onDrawerToggle } = props;

  const [value, setValue] = React.useState(0);

  function handleChange(event, newValue) {

   props.history.push("/report/"+newValue+"/"+props.state.fromDate+"/"+props.state.toDate)
  }
console.log(props)
  return (
    <React.Fragment>
    
      <AppBar
        component="div"
        className={classes.secondaryBar}
        color="primary"
        position="static"
        elevation={0}
      >
        <Tabs value={props.filter} textColor="inherit"   variant="scrollable" onChange={handleChange}>
          <Tab value={'daily'} textColor="inherit" label="Daily Sales" />
          <Tab value={'product'} textColor="inherit" label="Sales by Product" />
          <Tab value={'customer'} textColor="inherit" label="Sales by customer" />
          <Tab value={'Sales by payment mode'} textColor="inherit" label="Sales by payment mode" />
         
        </Tabs>
       
      </AppBar>
    </React.Fragment>
  );
}

Header.propTypes = {
  classes: PropTypes.object.isRequired,
  onDrawerToggle: PropTypes.func.isRequired,
};

const mapStateToProps = state => {
  console.log(state.product.attachment)
      return {
        attachment:state.product.attachment,
        drawer:state.common.drawer
       }
  };
  

export default withRouter(withStyles(styles)(connect(mapStateToProps,{toggleDrawer}) (Header)));