import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Dia from './dia'
import axios from 'axios'
const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: theme.spacing(3),
  },
}));

function getSteps() {
  return ['Seen', 'Dispatched', 'Shipped','Delivered'];
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return ``;
    case 1:
      return 'An ad group contains one or more ads which target a shared set of keywords.';
    case 2:
      return ``;
    case 3:
    return "Item has been delivered"
   
  }
}

export default function VerticalLinearStepper(props) {
  let status=0
  if(props.data.status!==null){
    if(props.data.status>4){
      status=4
    }
    else{
      status=props.data.status
    }
  }
  console.log(status)
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(status);
  const [dia, setDia] = React.useState({open:false,message:'',action:0});
  const steps = getSteps();

  const handleNext = () => {
    setActiveStep(prevActiveStep => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  const handleConfirm=()=>{
axios.post('https://green-planet.herokuapp.com/public/admin/updateOrder',{_id:props.data._id,status:status+1}).then(res=>{

  handleNext()
  setDia({open:false})
console.log(res)
})

  }

  return (
    <div className={classes.root}>
    <Dia open={dia.open} handleClose={()=>setDia({...dia,open:false})} message={dia.message} handleConfirm={handleConfirm}/>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((label, index) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
            <StepContent>
              <Typography>{getStepContent(index)}</Typography>
              <div className={classes.actionsContainer}>
                <div>
             
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={()=>setDia({open:true,message:'You are going to mark order as '+label+".This process is irreversible please confirm to continue ",action:0})}
                    className={classes.button}
                  >
                    {'Mark as '+label}
                  </Button>
                </div>
              </div>
            </StepContent>
          </Step>
        ))}
      </Stepper>
    
    </div>
  );
}