import axios from 'axios'

import Home from '../src/views/Dashboard/Dashboard'

import {connect} from 'react-redux';



class CreateProduct extends React.Component {
  constructor(props) {
    super(props);
    var currDate=new Date()
    this.state={
        fromDate:currDate.getTime()-(24*60*60*1000*70),
        toDate:currDate.getTime(),
       dataSales:{labels:[],series:[]},
       stats:{}

           }
  }
componentDidMount(){
    axios.post('https://green-planet.herokuapp.com/public/admin/getOrderStats').then(res=>{
        this.setState({stats:{...this.state.stats,openOrders:res.data[0].openOrders[0].count,revenue:res.data[0].revenue[0].totalSaleAmount}})
    console.log(res.data)
    })
    axios.post('https://green-planet.herokuapp.com/public/productAdmin/getstats').then(res=>{
        this.setState({stats:{...this.state.stats,outOfStock:res.data[0].outofStock[0].count,lowStock:res.data[0].lowStock[0].count}})
    console.log(res.data)
    })

    axios.post('https://green-planet.herokuapp.com/public/admin/userCount').then(res=>{
        this.setState({stats:{...this.state.stats,customers:res.data[0].count}})
    console.log(res.data)
    })

    axios.post('https://green-planet.herokuapp.com/public/admin/getOrders',{fromDate:this.state.fromDate,toDate:this.state.toDate,filter:{}}).then(res=>{
console.log(res)

var x=[]
var y=[]

function groupBy(list, keyGetter) {
    const map = new Map();
    list.forEach((item) => {
         const key = keyGetter(item);
         const collection = map.get(key);
         if (!collection) {
             map.set(key, [item]);
         } else {
             collection.push(item);
         }
    });
    return map;
}

const grouped = groupBy(res.data, data => {
    
   var date=new Date(data.createdAt).getTime()
   date=(date/(24*60*60*1000))
   return Math.floor(date)
})

var sorted=[...grouped.entries()].sort()
var month=['Jan','Feb','Mar','Apr','May','Jun','Jul','Aug','Sep','Oct','Nov','Dec']
sorted.map((data=>{
    var newDate=new Date(data[0]*60*24*60*1000)
var count=data[1].length
x.push(newDate.getDate()+' '+month[newDate.getMonth()])
y.push(count)
}))
  
 console.log(x)
 console.log(y)
this.setState({dataSales:{labels:x,series:y}})
console.log([...grouped.entries()].sort())

})
}



render(){


  return (
    <React.Fragment>
        <HeaderSecondary title="Dashboard"/>
<Home dataSales={this.state.dataSales} stats={this.state.stats} />
   
    </React.Fragment>
  );
}

}


const mapStateToProps = state => {

    return {
      attachment:state.product.attachment,
      products:state.product.products
     }
};

export default connect(mapStateToProps,{})(CreateProduct);
